import React, { useState } from 'react';
import PT from 'prop-types';

function Header({ color }) {
  const [left, setLeft] = useState(true);
  return (
    <header className="header">
      <h1>My Blog</h1>
      <div className="switch">
        <button
          type="button"
          className="slider"
          onClick={() => setLeft(!left)}
        />
      </div>
      <style jsx>{`
            .header {
                font-family: Roboto;
                box-sizing: border-box;
                width: 100%;
                height: 100px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                color: ${color};
            }

            .switch {
                width: 40px;
                height: 20px;
                border-radius: 10px;
                background: #fff;
            }

            .slider {
                height: 20px;
                width: 20px;
                border-radius: 10px;
                background: red;
                outline: none;
                transform: translateX(${left ? '0%' : '100%'});
            }
        `}
      </style>
    </header>
  );
}

Header.propTypes = {
  color: PT.string.isRequired,
};

export default Header;
